export default {
  props: ['hotsong'],
  data() {
    return {
      scrollTop: 0,
      old: {
        scrollTop: 0
      },
      url: ''
    };
  },
  mounted() {

  },
  computed: {
    // 是否播放状态切换
    isplay(){
      return function(id){
        return (id == this.$store.state.nowsong) && this.$store.state.musicisplay
      }
    }
  },
  methods: {
    playsong(id) {
      if (this.$store.state.nowsong !== id) {
        // 不相等就播放其他的歌
        uni.request({
          url: 'http://127.0.0.1:3000/song/url?id=' + id,
          method: 'GET',
          data: {},
          success: res => {
            const url = res.data.data[0].url

            this.$store.commit('getMusicAndPlay', {
              src: url,
              id: id
            })
          },
          fail: () => {},
          complete: () => {}
        });
        // 直接返回不执行后面
        return
      }
      // 判断是否在暂停状态
      if (this.$store.state.innerAudioContext.paused) {
        this.$store.commit('playMusic')
      } else {
        this.$store.commit('pauseMusic')
      }
    },
    upper: function(e) {

    },
    lower: function(e) {

    },
    scroll: function(e) {

      this.old.scrollTop = e.detail.scrollTop
    },
    goTop: function(e) {
      this.scrollTop = this.old.scrollTop
      this.$nextTick(function() {
        this.scrollTop = 0
      });
      uni.showToast({
        icon: "none",
        title: "纵向滚动 scrollTop 值已被修改为 0"
      })
    }
  }
}
